
import { Component, OnInit, ViewChild, ElementRef, NgZone } from '@angular/core';
import { AuthenticationService } from '../../services/Authentication.service';
import { AlertController, ModalController , NavParams} from '@ionic/angular';


@Component({
  selector: 'app-my-modalcalcu',
  templateUrl: './my-modalcalcu.page.html',
  styleUrls: ['./my-modalcalcu.page.scss'],
})
export class MyModalcalcuPage implements OnInit {


  display = '0';
  firstval: number = null;
  operator: any = null;
  newcursor = false;
  isc = false;
  iscomma = false;
  saldoactual: any;
  moneda: any;
  bottonn: any;
  bottonn2: any;
  userinfo: any = [];
  tipo: any;

  validasaldo: any;

  tituloxx: any;


  constructor( public alertController: AlertController,  private modalController: ModalController,
    private authenticationService: AuthenticationService,
    private navParams: NavParams
  ) { }


  click(val: any) {
    switch (val) {
      case 'ac':
        this.display = '0';
        this.firstval = null;
        this.operator = null;
        this.newcursor = false;
        break;
      case 'c':
        this.display = '0';
        this.isc = false;
        break;
      case '+/-':
        if (Math.sign(parseInt(this.display, 0)) === 1) {
          const sign = -Math.abs(parseInt(this.display, 0));
          this.display = sign.toString();
        } else if (Math.sign(parseInt(this.display, 0)) === -1) {
          const sign = Math.abs(parseInt(this.display, 0));
          this.display = sign.toString();
        } else {
          this.display = this.display;
        }
        break;
      case '%':
        this.addpercent();
        break;
      case ':':
        this.addoperator(':');
        break;
      case 'X':
        this.addoperator('X');
        break;
      case '-':
        this.addoperator('-');
        break;
      case '+':
        this.addoperator('+');
        break;
      case '=':
        if (this.firstval !== null && this.operator !== null) {
          this.calclast();
        }
        this.operator = null;
        break;
      case '0':
        this.addnumber('0');
        break;
      case '1':
        this.addnumber('1');
        break;
      case '2':
        this.addnumber('2');
        break;
      case '3':
        this.addnumber('3');
        break;
      case '4':
        this.addnumber('4');
        break;
      case '5':
        this.addnumber('5');
        break;
      case '6':
        this.addnumber('6');
        break;
      case '7':
        this.addnumber('7');
        break;
      case '8':
        this.addnumber('8');
        break;
      case '9':
        this.addnumber('9');
        break;
      case ',':
        this.addcomma();
        break;
    }
  }

  addcomma() {
    if (this.iscomma === false) {
      this.iscomma = true;
    } else {
      this.iscomma = false;
    }
  }

  addnumber(nbr: string) {
    if (nbr === '0') {
      if (this.newcursor === true) {
        this.display = nbr;
        this.newcursor = false;
      } else if (this.display !== '0') {
        if (this.iscomma === true) {
          this.display = `${this.display.toString()}.${nbr}`;
        } else {
          this.display = this.display.toString() + nbr;
        }
      } else if (this.display === '0') {
        if (this.iscomma === true) {
          this.display = `${this.display.toString()}.${nbr}`;
        }
      }
    } else {
      if (this.newcursor === true) {
        this.display = nbr;
        this.newcursor = false;
      } else if (this.display === '0') {
        if (this.iscomma === true) {
          if (this.display.toString().indexOf('.') > -1) {
            this.display = this.display.toString() + nbr;
          } else {
            this.display = `${this.display.toString()}.${nbr}`;
          }
        } else {
          this.display = nbr;
        }
      } else {
        if (this.iscomma === true) {
          if (this.display.toString().indexOf('.') > -1) {
            this.display = this.display.toString() + nbr;
          } else {
            this.display = `${this.display.toString()}.${nbr}`;
          }
        } else {
          this.display = this.display.toString() + nbr;
        }
      }
    }
    this.isc = true;
  }

  addpercent() {
    this.iscomma = false;
    const dispval = parseInt(this.display, 0) / 100;
    this.display = dispval.toString();
  }

  addoperator(op: string) {
    if (this.newcursor === false) {
      if (this.firstval === null) {
        if (this.iscomma === true) {
          this.firstval = parseFloat(this.display);
        } else {
          this.firstval = parseInt(this.display, 0);
        }
      }
      if (this.firstval !== null && this.operator !== null) {
        this.calclast();
      }
    }
    this.iscomma = false;
    this.operator = op;
    this.newcursor = true;
  }

  calclast() {
    switch (this.operator) {
      case ':':
        if (this.iscomma === true) {
          this.firstval = (this.firstval / parseFloat(this.display));
        } else {
          this.firstval = (this.firstval / parseInt(this.display, 0));
        }
        break;
      case 'X':
        if (this.iscomma === true) {
          this.firstval = (this.firstval * parseFloat(this.display));
        } else {
          this.firstval = (this.firstval * parseInt(this.display, 0));
        }
        break;
      case '-':
        if (this.iscomma === true) {
          this.firstval = (this.firstval - parseFloat(this.display));
        } else {
          this.firstval = (this.firstval - parseInt(this.display, 0));
        }
        break;
      case '+':
        if (this.iscomma === true) {
          this.firstval = (this.firstval + parseFloat(this.display));
        } else {
          this.firstval = (this.firstval + parseInt(this.display, 0));
        }
        break;
    }
    this.display = this.firstval.toString();
  }

  dismissModal() {     this.modalController.dismiss({data: 'close'});    }
 ngOnInit() {

  this.tituloxx = this.navParams.get('title');
  this.moneda = this.navParams.get('moneda');
  this.bottonn = this.navParams.get('botonn');
  this.bottonn2 = this.navParams.get('botonn2');
  this.tipo = this.navParams.get('tipo');
  this.validasaldo = this.navParams.get('validarsaldo');

  this.userinfo = this.authenticationService.getcopiauser();

  this.authenticationService.getReg(this.userinfo[0]._id + '/personadispid/' +
  this.userinfo[0].idempresa._id, 'personals', 0).then((result) => {
    this.saldoactual		=	result[0].saldo;


  }, (err) => {
    this.authenticationService.presentAlert(err.error , 'Precaución' , '');

  });



  }

  async iniciacobro0() {
    this.modalController.dismiss({data: this.display});
    /*
    const alert = await this.alertController.create({
      header: '¿Seguro de realizar un ' + this.bottonn2 + ' ?',
      message: this.moneda + ' ' + this.display ,
      buttons: [
        {
          text: 'No',
         cssClass: 'secondary',
          handler: (blah) => {
          }
        }, {
           text: 'Si',
           handler: () => {

            this.modalController.dismiss({data: this.display});

           }
         }
       ]
     });
     await alert.present();
     */
  }
async iniciacobro() {

  if (this.validasaldo === 'si') {


    this.authenticationService.getReg(this.userinfo[0]._id + '/personadispid/' +
    this.userinfo[0].idempresa._id, 'personals', 0).then((result) => {
      this.saldoactual		=	result[0].saldo;
      if (Number(this.display) <= Number(result[0].saldo)) {

        if (this.tipo === 'pagoqr') {
          this.modalController.dismiss({data: this.display});
        } else {
          this.iniciacobro0();
        }



      } else {
        this.authenticationService.presentAlert('Saldo insuficiente para realizar transacción' , 'Precaución' , '');

      }

    }, (err) => {
      this.authenticationService.presentAlert(err.error , 'Precaución' , '');

    });


  } else {
    if (this.tipo === 'vendersaldo') {
      this.modalController.dismiss({data: this.display});
    } else {
      this.iniciacobro0();
    }

  }





}
}
